.speech-bubble {
  position: relative;
  width: 250px;
  height: 62px;
  // line-height: 50px;
  color: #ffffff;
  // text-align: center;
  padding: 10px;
  line-height: 16px;
  background: #e8e8e8;
  color: black;
  border-radius: 6px;
  margin-left: -121px;
  margin-top: -138px;
  box-shadow: 5px 5px 19px 0px #a3a3a3;
  opacity: 0.95;
  font-family: $font-light;
}

.speech-bubble:after {
  content: "";
  position: absolute;
  border-style: solid;
  border-width: 15px 10px 0;
  border-color: #e8e8e8 transparent;
  display: block;
  width: 0;
  z-index: 1;
  bottom: -15px;
  left: 110px;
}

