#map {
  width: 100%;
  height: 500px;
}

body {
  min-width: calc(#{$screen-width} + (#{$screen-padding} * 2));
  -webkit-overflow-scrolling: touch;
  &.modal-open {
    overflow: hidden !important;
    &:before {
      content: "";
      top: 0;
      left: 0;
      height: 100%;
      width: 100%;
      background-color: rgba(black, 0.75);
      position: fixed;
      z-index: 99;
    }
  }
}

.lightbox-background {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 99;
  display: none;
}

.modal {
  position: fixed;
  width: 60%;
  height: 50%;
  background: white;
  z-index: 101;
  left: 20%;
  top: 25%;
  // top: 25%;
  // border-radius: $border-radius;
  display: none;
  overflow: scroll;
  line-height: 20px;
  &.active {
    display: block;
  }
  & .cross {
    position: absolute;
    right: 1em;
    width: 1.1em;
    height: 1.1em;
    background: url(../img/svg/cross-out.svg) no-repeat;
    cursor: pointer;
    z-index: 3;
  }
  & .modal-content {
    padding: 1em;
    & h4 {
      font-weight: normal;
      font-family: "helveticaneuecyrbold";
    }
    & .col {
      width: 50%;
      float: left;
    }
  }
}
.modal-open {
  & .main {
    // -webkit-filter: blur(5px);
    //    -moz-filter: blur(5px);
    //      -o-filter: blur(5px);
    //     -ms-filter: blur(5px);
    //         filter: blur(5px);
  }
  & .lightbox-background {
    display: block;
  }
}

#modal-video {
  overflow: hidden;
  width: 800px;
  height: 450px;
  left: calc(50% - 400px);
  top: calc(50% - 225px);
  & #youtube-video {
    width: 100%;
    height: 100%;
  }
}

.caret {
  background: transparent url(../img/svg/dropdown-gray.svg) no-repeat
    calc(100% - 13px) 0;
  background-size: 14px;
  height: 15px;
  width: 27px;
  right: 0 !important;
  top: 17px;
  border: 0 !important;
}

.dropdown-toggle:focus {
  outline: none !important;
}

.form-errors {
  display: inline-block;
  margin: 14px;
  margin-bottom: 0;
  line-height: $line-height-main + px;
  color: $text-red;
  white-space: nowrap;
}

.link-gray {
  color: $text-grey;
}

.close-cross {
  width: 25px;
  float: right;
  padding: 20px;
  cursor: pointer;
}

.default-height {
  line-height: $line-height-main + px;
}

.double-spacing {
  margin-bottom: $line-height-main + px;
}

.half-spacing {
  margin-bottom: ($line-height-main / 2) + px;
}

.one-and-half-spacing {
  margin-bottom: ($line-height-main / 1.5) + px;
}

.half-spacing-top {
  margin-top: ($line-height-main / 2) + px;
}

.tutor-name {
  font-size: 18px;
}

.font-medium {
  font-family: $font-medium;
}

p {
  line-height: 21px;
}

h3.padding-top {
  margin-top: 32px;
}

.absolute-message {
  position: absolute;
  width: 100%;
  top: 0;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.back-arrow {
  & img {
    height: 14px;
    margin-right: 5px;
  }
  display: flex;
  align-items: center;
  margin-bottom: 11px;
}

.pdf-download {
  justify-content: flex-end;
  font-size: 12px;
  & a {
    // text-decoration: none;
  }
  & span {
    margin-left: 5px;
  }
}

.subject-list-flex {
  display: flex;
  & > div {
    flex: 1;
  }
}

ol,
ul {
  line-height: $line-height-main + px;
}

.page-not-found-wrapper {
  min-height: 400px;
  display: flex;
  justify-content: center;
  align-items: center;
  & .page-not-found {
    text-align: center;
    & h1 {
      font-family: $font-regular;
    }
    & ul {
      list-style: none;
      padding: 0;
    }
  }
}

.header-menu-item-order {
  background: $color-green;
  &:hover {
    background: darken($color-green, 20);
  }
}

.order-right-block {
  padding: 10px;
  background: #d9eede;
  border-radius: 4px;
  line-height: $line-height-main + px;
  & center {
    margin: 20px 0 10px;
  }
}

.price-number {
  font-size: 18px;
}

.payment-types {
  display: inline-block;
  height: 20px;
  top: 3px;
  position: relative;
  margin-left: 3px;
}

.signup-bottom-price {
  text-align: center;
  line-height: $line-height-main + px;
  & .signup-bottom-price-deadline {
    margin-top: 10px;
  }
}

.signup-bottom {
  margin-top: 10px;
}

#deadline-bar {
  background: #fffccc;
  border: 1px solid #ebdd53;
  border-left: 0;
  border-right: 0;
  padding: 8px 0;
  text-align: center;
  color: $text-red;
}

/*      */
.header-menu-wrap__img {
  & #deadline-bar {
    display: none !important;
  }
}

/* IE10+ specific styles go here */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .main {
    display: block !important;
  }
}

.tutor-rating {
  background-color: $menu-blue;
  color: white;
  font-family: $font-medium;
  padding: 2px 4px;
  border-radius: 2px;
}

.tutor-rating-line {
  margin-top: 8px;
}

.address-block-bottom {
  clear: both;
  padding-top: 18px;
}

.footer-icon {
  display: flex;
  align-items: center;
  margin-bottom: 30px;
  line-height: $line-height-main + px;
  & img {
    height: 30px;
    margin-right: 12px;
  }
}

.menu-header {
  & a {
    color: #ecdede !important;
    text-decoration: underline;
    font-size: 18px;
    font-family: "helveticaneuecyrbold";
    margin: 10px 0 16px;
    &:hover {
      text-decoration: none;
    }
  }
  & > span {
    color: #ecdede !important;
    font-size: 18px;
    font-family: "helveticaneuecyrbold";
    margin: 10px 0 16px;
    padding: 6px 0 !important;
    display: block;
    cursor: default;
  }
}

.menu-desc {
  color: lightgray;
  font-size: 12px;
  line-height: 0;
  top: -6px;
  position: relative;
}

.footer-links {
  margin-top: 30px;
  & a {
    margin-right: 10px;
    font-size: 12px;
  }
}

.img-banner {
  text-align: center;
  margin-bottom: 18px;
}

.header-message {
  text-align: center;
  background: #ce171f;
  color: white;
  padding: 8px 0;
  line-height: 18px;
  &__first-line {
    font-size: 16px;
    margin-bottom: 10px;
    font-weight: bold;
  }
}

@media only screen and (max-width: 1000px) {
  .full-width-wrapper:not(.banner-block) {
    margin-left: -20px !important;
  }
  .banner-block.right {
    margin-left: -20px !important;
  }
}

$banner-block-gradient-color: #f2f2f2;
$line-height: 21px;
$line-height-small: 17px;

.banner-block-full-width-wrapper {
  background: $banner-block-gradient-color;
  position: relative;
  & .lines {
    width: 490px;
    float: right;
    & .line {
      display: flex;
      align-items: center;
      font-size: 17px;
      margin-bottom: 14px;
      & img {
        height: 26px;
        width: 26px;
        margin-right: 6px;
      }
      & span {
        text-align: left;
      }
      &:last-of-type {
        margin-bottom: 0;
      }
    }
  }
  & .fa-check {
    margin-right: 12px;
  }
  & .banner-block {
    margin-bottom: 50px !important;
  }
}

.banner-block {
  z-index: 1;
  $gradient-offset: 230px;
  height: calc(100vw * 0.39);
  min-height: 600px;
  margin-top: -19px;
  width: calc(100vw - ((100vw - 960px) / 2));
  position: relative;
  margin-bottom: 100px;
  &__image {
    background-repeat: no-repeat;
    background-size: cover;
    background-color: white;
    height: 100%;
    top: 0;
    position: absolute;
    width: calc(100% - #{$gradient-offset});
  }
  & .btn-calc {
    margin-top: 20px;
    & img {
      height: 17px !important;
    }
    & span {
      font-size: 14px !important;
      top: 1px;
      position: relative;
    }
  }
  & .image-link {
    position: absolute;
    z-index: 11;
    top: 14px;
    font-size: 12px;
    font-weight: 600;
    & img {
      height: 16px;
      margin-right: 5px;
    }
  }
  & .info {
    text-align: right;
    justify-content: flex-end;
    position: inherit;
    z-index: 10;
    display: flex;
    align-items: center;
    height: 100%;
    &__items {
      height: 100%;
      & > div {
        height: 50%;
        &:nth-child(2n) {
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          margin-top: -90px;
        }
      }
    }
    & h1 {
      text-align: right !important;
      padding-top: 150px !important;
      padding-bottom: 0 !important;
      margin-top: 0 !important;
      margin-bottom: 0 !important;
      font-weight: 800;
      font-size: 45px;
      width: 125%;
      margin-left: -25%;
      font-family: $font-bold;
    }
    & .quote {
      font-size: 16px;
      font-style: italic;
    }
    & .tutor-title {
      margin-top: 18px;
      font-style: italic;
      & b {
        font-size: 20px !important;
        display: block;
        margin-bottom: 5px;
      }
      & div {
        font-size: 17px !important;
        line-height: $line-height-small;
      }
    }

    & .button-green-large {
      margin-top: 80px;
    }

    & > div {
      width: 515px;
    }
  }
  & .gradient {
    background: linear-gradient(
      to left,
      $banner-block-gradient-color $gradient-offset,
      rgba($banner-block-gradient-color, 0) 95%
    );
    overflow: hidden;
    position: absolute;
    top: 0;
    height: 100%;
    width: 70%;
    left: auto;
    right: 0;
  }
  &.left {
    margin-left: 0;
    margin-right: calc((100vw - 960px) / 2 * -1);
    background-position: 230px center;
    & .info {
      justify-content: flex-start;
      text-align: left;
      & h1 {
        margin-left: 0;
        text-align: left !important;
      }
    }
    & .banner-block__image {
      right: 0;
      background-position: center center;
    }
    & .gradient {
      left: 0;
      right: auto;
      background: linear-gradient(
        to right,
        $banner-block-gradient-color $gradient-offset,
        rgba($banner-block-gradient-color, 0) 95%
      );
    }
  }
  &.right {
    min-width: 980px !important;
    & .banner-block__image {
      left: 0;
      background-position: center center;
    }
    & .image-link {
      right: 0px;
    }
  }
}

.more-button {
  margin: 35px 0 10px;
}

.btn-border {
  min-width: 168px;
  font-family: "helveticaneuecyrbold";
}

.gallery-loading-backdrop {
  background-color: #383838;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  .spinner {
    -webkit-box-flex: 0;
    -ms-flex: none;
    flex: none;
    width: 100px;
    height: 50px;
    text-align: center;
    font-size: 10px;
    & > div {
      background-color: #fff;
      height: 100%;
      width: 7px;
      margin: 0 3px 0 0;
      display: inline-block;
      -webkit-animation: sk-stretchdelay 1.2s infinite ease-in-out;
      animation: sk-stretchdelay 1.2s infinite ease-in-out;
    }
    .rect2 {
      -webkit-animation-delay: -1.1s;
      animation-delay: -1.1s;
    }
    .rect3 {
      -webkit-animation-delay: -1s;
      animation-delay: -1s;
    }

    .rect4 {
      -webkit-animation-delay: -0.9s;
      animation-delay: -0.9s;
    }

    .rect5 {
      -webkit-animation-delay: -0.8s;
      animation-delay: -0.8s;
    }
  }
}
