/* Components */
.top-message {
  background: #fef9de;
  border-bottom: 1px solid #ffe65c;
}

.top-message-inner {
  width: 960px;
  margin: 0 auto;
  padding: 7px 0;
  font-family: 'helveticaneuecyrregular';
  color: #af0000;
  text-align: center;
  position: relative;
}

.licence-list {
  padding: 0 0 30px 0;
  margin: 0;
  list-style: none;
}

.licence-list li {
  line-height: 21px;
}

.header {
  padding: 0 0 19px 0;
  z-index: 99;
  position: sticky;
  top: 0;
}

.header-inner {
  position: relative;
  width: 960px;
  margin: 0 auto;
  padding: 24px 0 15px 0;
  text-align: center;
}

.header-inner:after {
  content: "";
  display: block;
  clear: both;
}

.header-inner .btn-fill {
  margin-top: 12px;
}

.header-logo {
  display: block;
  background: url("../img/svg/logo.svg") no-repeat;
  width: 190px;
  height: 67px;
  overflow: hidden;
  text-indent: -3000px;
  float: left;
  background-size: 100%;
}

.header-title {
  border-left: 1px solid #e5e5e5;
  font-size: 12px;
  line-height: 16px;
  font-family: "helveticaneuecyrregular";
  float: left;
  padding: 0 0 0 15px;
  margin: 5px 0 0 15px;
}

.header-contacts {
  float: right;
  font-family: "helveticaneuecyrregular";
  padding: 5px 0 0 0;
}

.header-contacts-phone {
  font-size: 24px;
}

.header-contacts-worktime {
  font-size: 14px;
  text-align: right;
  padding: 5px 0 0 0;
}

.header-menu {
  height: 44px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
  position: relative;
  z-index: 2;
  left: -14px;
  line-height: inherit;
  display: flex;
  justify-content: center;
}

.header-menu-wrap {
  background: #f3e9d4;
  position: relative;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.8);
}

.header-menu-wrap__img {
  background: url("/img/background/main.jpeg") no-repeat 50% 50%;
  height: 600px;
  background-size: cover;
}

.header-menu-wrap__img:before {
  content: "";
  display: block;
  position: absolute;
  left: 0;
  top: 0;
  height: 44px;
  width: 100%;
  background: rgba(0, 0, 0, 0.4);
}

.header-menu-wrap__img:after {
  content: "";
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.7);
}

.header-menu-wrap .header-menu .header-menu-item__active {
  background: rgba(0, 0, 0, 0.5);
}

.header-menu-item {
  float: left;
  padding: 0 14px;
  font-size: 14px;
  font-family: "helveticaneuecyrregular";
}

.header-menu-item__active {
  background: #6185ad;
}

.header-menu-item:hover {
  background: #ae4037;
}

.header-menu-item:hover a {
  color: white;
}

.header-menu-item a {
  display: block;
  height: 30px;
  padding: 14px 0 0 0;
  text-decoration: none;
  color: #ae4037;
  font-family: "helveticaneuecyrmedium";
  white-space: nowrap;
}

.header-img {
  position: relative;
  z-index: 1;
  color: #fff;
  text-align: left;
  height: calc(100% - 44px);
  display: flex;
  align-items: center;
  padding: 0;
  width: 960px;
  margin: 0 auto;
}

.header-img p {
  line-height: 24px;
  font-size: 15px;
  width: 80%;
}

.header-img button {
  margin-top: 20px;
}

.header-img-title {
  font-family: "helveticaneuecyrultralight";
  font-size: 64px;
  padding: 0 0 20px 0;
}

.header-img-title__large {
  font-family: "helveticaneuecyrultralight";
  font-size: 132px;
  line-height: 128px;
}

.header-img .button-green-large {
  margin: 10px 0 0 0;
}

.header .button-green-large__enrollment {
  position: absolute;
  font-weight: 100;
  top: 32px;
  left: calc(50% - 87px);
}

.lk-wrapper {
  position: absolute;
  right: 0;
  top: 6px;
}

.lk-wrapper img {
  height: 12px;
  top: 2px;
  position: relative;
}

.lk-wrapper a {
  font-size: 12px;
  font-family: "helveticaneuecyrregular";
}

.header-contact {
  float: right;
}

.footer {
  background: #3b3b3b;
  color: #fff;
  font-family: "helveticaneuecyrregular";
  font-size: 14px;
  font-weight: normal;
  width: 100%;
  padding-bottom: 20px;
  margin-top: 30px;
}

.footer a {
  color: #fff;
}

.footer-inner {
  width: 960px;
  margin: 0 auto;
  padding: 20px 0 0 0;
}

.footer-title {
  color: #ababab;
  font-family: 'helveticaneuecyrmedium';
  padding: 0 0 20px 0;
}

.footer-popular {
  float: left;
  width: calc(400px - 20px);
}

.footer-menu {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 21px;
}

.footer-menu li {
  padding: 0 0 2px 0;
}

.footer-main {
  float: left;
  width: calc(390px - 20px);
}

.footer-contacts {
  float: left;
  width: 210px;
}

.footer-copyright {
  text-align: center;
}

.footer-copyright:before {
  content: '';
  display: block;
  clear: both;
}

.address-left {
  width: calc(470px - 20px);
  float: left;
  padding: 0 30px 0 0;
}

.address-right {
  float: left;
  width: calc(500px - 20px);
  overflow: hidden;
  border-radius: 6px;
  margin-bottom: 36px;
}

.address-list {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: initial;
}

.address-list-item {
  padding: 0 0 10px 15px;
  position: relative;
}

.address-list-item:after {
  content: '';
  display: block;
  clear: both;
}

.address-list-item-info {
  float: right;
  color: #818181;
  text-align: right;
  width: 175px;
}

.address-list-item-more {
  color: #818181;
}

.address-list-item__main {
  padding-bottom: 35px;
}

.address-more {
  margin: 0;
  padding: 0;
  list-style: none;
}

.address-more li:not(:last-child) {
  margin-bottom: 21px;
}

.address-messages {
  font-family: 'helveticaneuecyrbold';
  color: #a30000;
  line-height: 24px;
  padding: 45px 0 0 0;
}

.questions {
  margin: 0;
  padding: 0 0 30px 0;
  list-style: none;
}

.questions-item {
  line-height: 21px;
}

.questions-item-title {
  color: #960000;
  display: inline;
  border-bottom: 1px dashed #960000;
  padding: 0 0 1px 0;
  cursor: pointer;
}

.questions-item-answer {
  display: none;
  color: #818181;
  padding: 14px 0;
  line-height: 21px;
}

.questions-item .has-image {
  display: block;
  text-align: center;
}

.zoom-hint {
  display: none;
}

.reviews {
  margin: 0;
  padding: 0;
  list-style: none;
  line-height: 21px;
}

.reviews-item {
  padding: 0 0 25px 0;
}

.reviews-item-signature {
  padding: 0;
  font-size: 12px;
  color: #818181;
}

.review-rating {
  font-family: 'helveticaneuecyrmedium';
}

.price {
  padding: 0 0 30px 0;
}

.price-title {
  font-family: 'helveticaneuecyrmedium';
  background: #767676;
  color: white;
  padding: 10px;
  margin: 0 0 12px 0;
}

.price-list {
  border-collapse: collapse;
  border-spacing: 0;
  table-layout: fixed;
  width: 100%;
}

.price-list thead {
  background: #767676;
  color: white;
  border-radius: 3px;
  font-family: 'helveticaneuecyrmedium';
}

.price-list thead td {
  padding: 10px;
  vertical-align: top;
}

.price-list tbody td {
  padding: 0 0 15px 10px;
  line-height: 21px;
}

.price-list tbody td .button-green-small {
  margin-left: 5px;
  font-family: "helveticaneuecyrregular";
  margin-top: -1px;
  position: relative;
  font-size: 12px;
  padding: 2px 8px;
  float: right;
}

.price-list tbody tr.margin-top td {
  padding-top: 30px;
}

.price-list tbody tr.line-separated {
  border-top: 1px solid #acacac;
}

.price-list tbody tr.line-separated td {
  padding: 8px 10px;
}

.price-list tbody tr.after-line-separated td {
  padding-top: 10px;
}

.enrollment .form-row-inner {
  width: 320px;
}

.enrollment .form-row-messages {
