  .ng-image-gallery-modal
  .ng-image-gallery-content
  .control-icons-container,
.ng-image-gallery
  .ng-image-gallery-modal
  .ng-image-gallery-content
  .destroy-icons-container {
  position: absolute;
  right: 15px;
  top: 15px;
}
