.galleria-bubble.img-bubble.active {
  height: 110px !important;
  width: 110px !important;
}

.title {
  position: relative;
  z-index: 99;
  color: white;
  text-align: center;
  margin: 19px 0px;
  font-size: 16px;
}

.mobile-gallery {
  .ng-image-gallery-loader {
    top: 2 * $gallery-common-indent !important;
    bottom: 2 * $gallery-common-indent !important;
  }

  .ng-image-gallery-content .galleria {
    padding: 60px 0 !important;
  }

  .title {
    margin: 0;
    position: absolute;
    bottom: calc(((100vh - (2 * 100vw / 3)) / 2) - 5em);
    .iphone4 & {
      bottom: calc(((100vh - (2 * 100vw / 3)) / 2) - 5em);
    }
    width: 100%;
    span {
      position: relative;
      z-index: 99;
      display: inline-block;
      margin: 10px 10px;
      cursor: pointer;
      &:nth-child(2) {
        min-width: 4em;
      }
      &:first-child i {
        background: url(../img/svg/gallery-prev.svg);
      }
      &:last-child i {
        background: url(../img/svg/gallery-next.svg);
      }
      & i {
        background-size: contain !important;
        vertical-align: middle;
        display: inline-block;
        padding: 0.65rem 0.6rem;
        margin: 0.2rem 0.4rem;
      }
    }
  }
}

.gallery-photo-master-info {
  height: 75%;
  width: 100%;
  display: flex;
  position: relative;
}

.gallery-master-info {
  width: 380px;
  color: white;
  padding: 25px;
  padding-top: 0;
  box-sizing: border-box;
  line-height: 20px;
  overflow: hidden;
  position: absolute;
  right: 0;
  & .master-photo {
    & img {
      height: 90px;
      width: 90px;
      border-radius: 50%;
    }
  }
  & > div {
    margin-bottom: 30px;
    &:first-of-type {
      // margin-top: 70px;
      width: 90%;
    }
  }
}

.gallery-components {
  & > div {
    display: flex;
    align-items: flex-end;
    & span {
      &:first-of-type {
        width: 235px;
        display: inline-block;
      }
      &:last-of-type {
        text-align: right;
        flex: 1;
      }
    }
    &.gallery-component-sum {
      border-top: 1px solid rgba(white, 0.7);
      margin-top: 12px;
      padding-top: 10px;
      & span {
        &:last-of-type {
          font-family: $font-bold;
        }
      }
    }
  }
}

.gallery-flow-control {
  font-family: $font-medium;
  text-align: center;
  font-size: 22px;
  margin-bottom: 30px;
  color: white;
  & > span {
    top: -6px;
    position: relative;
  }
}

.gallery-bottom-controls {
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
}

.gallery-flow-control-arrow {
  background-color: rgba(0, 0, 0, 0.2);
  height: 60px;
  width: 60px;
  display: inline-block;
  border-radius: 50%;
  line-height: 78px;
  color: rgba(white, 0.5);
  cursor: pointer;
  -webkit-transition: background-color 0.15s ease-in-out;
  transition: background-color 0.15s ease-in-out;
  & img {
    height: 32px;
    opacity: 0.7;
  }
  &:first-of-type {
    margin-right: 50px;
  }
  &:last-of-type {
    margin-left: 50px;
  }
  &:hover {
    background-color: rgba(0, 0, 0, 0.6);
  }
}

.galleria-bubbles {
  -webkit-transition: margin 0.3s ease-in-out;
  transition: margin 0.3s ease-in-out;
}

.gallery-close {
  position: absolute;
  right: 0;
  text-align: right;
  z-index: 2;
  & > div {
    text-align: center;
    margin: 0 !important;
    line-height: 72px !important;
  }
  & img {
    height: 26px !important;
  }
}

.gallery-days-to-complete {
  & span {
    float: none !important;
    width: auto !important;
  }
}

